const string =`.skin *{box-sizing: border-box;margin: 0;padding: 0;}
.skin *::before, .skin *::after{box-sizing: border-box;}

.skin{
    background: #ffe600;
    height: 50vh;
    position: relative;
}
.nose{
    border: 10px solid red;
    border-color:black transparent transparent;
    border-bottom: none;
    width: 0px;
    height: 0px;
    position: absolute;
    left: 50%;
    top:145px;
    margin-left: -10px;
    z-index:2
}
@keyframes wave{
    0%{
        transform: rotate(0deg);
    }
    33%{
        transform: rotate(5deg);
    }
    66%{
        transform: rotate(-5deg);
    }
    100%{
        transform: rotate(0deg);
    }
}

.nose:hover{
    transform-origin : center bottom;
    animation: wave 300ms infinite linear;
}

.yuan{
    position: absolute;
    width: 20px;
    height: 6px;
    left: -10px;
    top:-16px;
    border-radius: 10px 10px 0 0;
    background: black;
}

.eye {
    border: 2px solid #000;
    width: 64px;
    height: 64px;
    position: absolute;
    left: 50%;
    top:100px;
    margin-left: -30px;
    background: #2e2e2e;
    border-radius: 50%;
}
.eye::before{
    content: '';
    display: block;
    border: 3px solid #000;
    width: 28px;
    height: 28px;
    background: #fff;
    border-radius: 50%;
    position: relative;
    left:8px;
    top:2px
}
.eye.left{
    transform: translateX(-150px);
}
.eye.right{
    transform: translateX(150px);
}
.mouth{
    width: 200px;
    height: 200px;
    position: absolute;
    left: 50%;
    top:170px;
    margin-left: -100px;
}
.mouth .up{
    position: relative;
    top:-20px;
    z-index: 1;
}
.mouth .up .lip{
    border: 3px solid black;
    height: 30px;
    width: 100px;
    background: #ffe600;
    position: absolute;
    left:50%;
    margin-left: -50px;
}
.mouth .up .lip.left{
    border-radius: 0 0 0 50px;
    border-top-color: transparent;
    border-right-color: transparent;
    transform: rotate(-15deg) translateX(-53px);
}
.mouth .up .lip.right{
    border-radius: 0 0 50px 0;
    border-top-color: transparent;
    border-left-color: transparent;
    transform: rotate(15deg) translateX(53px);
}
.mouth .up .san1{
    border: 3px solid red;
    border-color: black transparent transparent;
    border-bottom: none;
    width: 0px;
    height: 0px;
    position: relative;
    left: 50%;
    top:28px;
    margin-left: -2.5px;
    border-radius: 50%;
}

.mouth .down{
    height: 180px;
    position: absolute;
    top: 7px;
    width: 100%;
    overflow: hidden;
}

.mouth .down .yuan1{
    border: 3px solid black;
    width: 150px;
    height: 1000px;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -75px;
    border-radius: 75px/300px;
    background: #9b000a;
    overflow: hidden;
}

.mouth .down .yuan1 .yuan2{
    width: 200px;
    height: 300px;
    background: #ff485f;
    position: absolute;
    bottom: -155px;
    left: 50%;
    margin-left: -100px;
    border-radius: 100px;
}

.face{
    position: absolute;
    left: 50%;
    border: 3px solid black;
    width: 88px;
    height: 88px;
    top:200px;
    margin-left: -44px;
}
.face > img {
    position: absolute;
    top: 50%;
    left: 50%;
}
.face.left{
    transform: translateX(-180px);
    background: #ff0000;
    border-radius: 50%;
}
.face.left > img{
    transform: rotateY(180deg);
    transform-origin: 0 0;
}
.face.right{
    transform: translateX(180px);
    background: #ff0000;
    border-radius: 50%;
}`

export default string;